import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpClient as HttpClient2 } from 'src/app/core/http.client';

import { Router } from '@angular/router';


@Component({
  selector: 'app-anunciar',
  templateUrl: './anunciar.component.html',
  styleUrls: ['./anunciar.component.css']
})
export class AnunciarComponent implements OnInit {

  constructor(private router: Router,private http: HttpClient, private _http: HttpClient2) { }

  ngOnInit() {
    var data = sessionStorage.getItem('login');

    if(!data){
      this.router.navigate(['login']);
    }
  }

  public cep: string;
  public cepJson: any;

  title: string;
  description: string;
  type:string;

  buscaCep(){
    this.http.get(`https://viacep.com.br/ws/${this.cep}/json/`)
    .subscribe(data => this.cepJson = data)
  }

  async doService(){

    let email = await sessionStorage.getItem('login');

    this._http.post(`/service/register`, {
      email,
      title: this.title,
      description: this.description,
      type: this.type,
      address: this.cepJson
    }).subscribe(data => {
      let resp : any = data
      if(!resp.error){
        alert('Anúncio criado com sucesso')
      }else{
        alert('Erro ao criar anúncio!')
      }
    })
  }

}
