import { Component, OnInit } from '@angular/core';
import { HttpClient } from 'src/app/core/http.client';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cadastro',
  templateUrl: './cadastro.component.html',
  styleUrls: ['./cadastro.component.css']
})
export class CadastroComponent implements OnInit {

  nome: string;
  email: string;
  senha: string;
  resenha: string

  constructor(private http: HttpClient, private router: Router) { }

  ngOnInit() {

  }

  doRegister(){
    if(this.senha != this.resenha){
      alert('Senhas não coincidem')
    }else{
      this.http.post(`/user/register`,
      {name: this.nome, email: this.email, password: this.senha })
      .subscribe(data => {
        if(data.error){
          alert("Erro ao cadastrar!")
        }else{
          sessionStorage.setItem('login', data.register.email);
          this.router.navigate(['anunciar']);

        }
      })
    }
  }

}
