import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from 'src/app/core/http.client';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  query: any;
  private sub: any;

  queryParams:any  = {};

  servicesList: any;

  inputSearch: string;

  barbeiro = false;
  pedreiro = false;
  pintor = false;

  pavuna = false;
  nilopolis = false;
  duquedecaxias = false;

  constructor(private route: ActivatedRoute, private http: HttpClient) {
  }

  public capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
       this.query = params['query'];
       console.log(this.query)
    });

    this.search({query: this.query});

    // console.log(this.queryParams)
  }

  public searchByForm(){
    console.log(this.barbeiro, this.pedreiro, this.pintor)

    let filter = {query: this.inputSearch, type:[], city:[]};

    if(this.barbeiro)
      filter.type.push("barbeiro")
    if(this.pedreiro)
      filter.type.push("pedreiro")
    if(this.pintor)
      filter.type.push("pintor")

    if(this.nilopolis)
      filter.city.push("Nilópolis")
    if(this.pavuna)
      filter.city.push("Pavuna")
    if(this.duquedecaxias)
      filter.city.push("Duque de Caxias")

    this.search(filter);
  }

  public search(params: any = {}){
    this.http.post(`/service/get`,
    params)
    .subscribe(data => {

      let services = data.services;
      services.map(async service => {
        service.type = this.capitalizeFirstLetter(service.type);
      })
      // console.log(services);
      this.servicesList = data.services;

    })
  }
}
